/* Add hover effect to images */
.achievement img {
    max-width: 400px;
    margin-bottom: 25px;
    margin-left: 25px;
    border-radius: 150px;
    transition: transform 0.3s, box-shadow 0.3s; /* Add transitions for smooth effect */
  }
  
  /* Apply hover effect to paragraph */
  .achievement p {
    background: linear-gradient(to bottom, #ff004f, #080808); /* Add gradient background */
    padding: 10px;
    border-radius: 10px;
    overflow: hidden; /* Hide overflowing content */
    max-height: 0; /* Initially hide the content */
    opacity: 0; /* Initially hide the text */
    transition: max-height 0.5s, opacity 0.3s, transform 0.5s; /* Add transitions for animation */
  }
  
  .achievement:hover p {
    max-height: 300px; /* Expand the box on hovering */
    opacity: 1; /* Show the text on hovering */
    transform: translateY(60%); /* Move the box downward */
    transition-delay: 0.3s; /* Delay the transition to match the falling effect */
  }
  
  



